.sessions{
    position: relative;
    background: #FF0000;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.0212249);
    border-radius: 5px;
    /*height: 100%;
    min-height: 26rem;
    max-height: 30rem;   
    flex-basis: 30%;
    max-width: 30rem;*/
    /*grid-column: 1 / 3;
    grid-row: 6 / 9;*/
    grid-area: 3 / 1 / 5 / 2;
}

.sessions_chart{
    width: 100%;
    height: 50%;
}

.sessions h2{
    position: absolute;
    top: 0;
    left: 0;
    color: #FFFFFF;
    font-size: 1.5rem;
    font-weight: 500;
    opacity: 0.5;    
    padding: 3rem;    
}

.sessions_tooltip{
    background-color: white;
    font-size: .8rem;
    font-weight: 500;
    padding: 0 .5rem;
}

.sessions .recharts-tooltip-cursor{    
    stroke: rgba(0, 0, 0, 0.1);
    stroke-width: 100rem;
    transform: translateX(50rem);
    height: 100%;
}

.sessions_xAxis{
    display: flex;
    justify-content: space-around;
    position: absolute;
    bottom: 0;   
    color: rgba(255, 255, 255, 0.6);
    font-size: 1.4rem;
    padding: 2rem 0;
    width: 100%;
}