.not-found{
    width: 100%;
    height: 100%;
    min-height: 90vh;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.not-found_title{
    font-size: 10rem;
    padding: 3rem;
}

.not-found_subtitle{
    padding: 2rem;
}

.not-found_link{
    color: red;
    padding: 5rem;
}